//This is meant to be user entered data, stored in some ararys by the UI to be consumed by the app
let numberOfKids = 3;
let yearsBorn = [2022, 2024, 2026];
let names = ['Frankie', 'Suzie', 'Armando'];
//Require day care = true means nobody is home to take care of the kid from age 0-4
let requireDayCare = [true, true, true];
//Determines annual cost of college for ages 19-22
let privateCollege = [true, true, true];
//regular monthly cost whenver the kid is at home 0-18
const monthlyCost = 1500;
//cost of daycare if applicable, ages 0-4
const dayCareMonthly = 1000;
const privateCollegeAnnual = 60000;
const publicCollegeAnnual = 30000;
//standard tax credit for dependent
const taxCredit = 1500;

//Kid creator takes all the required variables related to the cost of a kid, and saves them as an object.  This is a factory function for kid objects
//Really only need a name and year born, default values are reasonable
//Important method is yearlyCost
const kidCreator = (_name='Kid', _yearBorn=2020, _requiresDayCare=false, _privateCollege=true) =>{
    return {
        _name,
        _yearBorn,
        _requiresDayCare,
        _privateCollege,
        //With above information, can calculate estimated cost for this kid for a given year
        yearlyCost(seekingYear){
            let cost = 0;
            //if seeking year is before birth year, cost is 0
            if(seekingYear<this._yearBorn){
                cost =0;
            //if seeking year occurs between age 0 and 4, and that kid requires day care, enter this section
            }else if(seekingYear-this._yearBorn<4 && this._requiresDayCare){
                //cost is regular monthly cost + daycare cost
                cost = (monthlyCost + dayCareMonthly)*12-taxCredit;
            //This section is for kids aged 0-4 that don't require daycare, or any kid aged 0-18 which wasn't caught by previous else if section
            }else if ((seekingYear-this._yearBorn<4 && !this._requiresDayCare) || seekingYear-this._yearBorn<=18 ){
                //cost does not include daycare
                cost = monthlyCost*12-taxCredit;
            //Seeking year falls within college years for private college
            }else if (seekingYear-this._yearBorn>18 && seekingYear-this._yearBorn<23 && this._privateCollege){
                cost = privateCollegeAnnual-taxCredit;
            //Seeking year falls within college years for public college
            }else if (seekingYear-this._yearBorn>18 && seekingYear-this._yearBorn<23 && !this._privateCollege){
                cost = publicCollegeAnnual-taxCredit;
            //Kid has left the nest, no cost
            }else if (seekingYear-this._yearBorn>22){
                cost = 0;
            }
            return cost;
        }  
    }
}

//Kiddos is an array which contains the kids invovled, each kid being an object defined above by kid creator
let kiddos = [];
//For loop to run through the kids defined by UI inputs and create a kid object for each using the defined values
for (i=0; i<numberOfKids; i++){
    kiddos[i]=kidCreator(names[i], yearsBorn[i], requireDayCare[i], privateCollege[i]);
}

//This function takes a year as a parameter and returns the annual cost of having kids for that year.  Will add up costs of multiple kids at the same time
const returnAnnualKidCost = year =>{
    const annualCostValues = [];
    kiddos.forEach(kid => {
        //for each kid, add the cost for the given year to an array
        annualCostValues.push(kid.yearlyCost(year));
    })
    //then reduce above array to sum up kid cost values into one total cost number
    const annualCost = annualCostValues.reduce((previousValue, currentValue)=>{
        return previousValue + currentValue;
    })
    return annualCost;
}

//export annual kid cost calculator to be used in overall life yearly calculations
module.exports = returnAnnualKidCost;